#root, .app {
  width: 100%;
  height: 100%;
}

.app {
  
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
}

.debug {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 1100;
  opacity: 0.1;
  transition: opacity 0.2s ease-in-out;
  display: none;
}

.debug:hover {
  opacity: 1;
}

.debug-controls {
  z-index: 1100;
  position: fixed;
  bottom: 0;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: calc(100%/2 - 40%);
  background: #fff;
  padding: 20px 20px;
  opacity: 0.2;
  transition: opacity 0.2s ease-in-out;
  border-radius: 10px 10px 0 0;
}

.debug-controls:hover {
  opacity: 1;
}

.debug-controls .control {
  position: relative;
  height: 40px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: stretch;
  justify-content: left;
}

/* 
.debug-controls .control > span {
  display: inline-block;
}*/

.debug-controls .control>span {
  min-width: 120px;
}

.debug-controls .control>div {
  width: 100%;
}

button+button {
  margin-left: 20px !important;
}

main {
  color: #fff;
  z-index: 500;
  position: absolute;
  width: 100%;
  pointer-events: none;
}

section {
  scroll-snap-align: start;
  position: relative;
  display: flex;
  min-width: 100%;
  min-height: 720px;
  height: 100vh;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  /* outline: 1px dashed fuchsia; */
}

section:not(:first-child)::before {
  content: '';
  background: #22ECF0;
  position: absolute;
  left: 0;
  top: 0;
  height: 2px;
  width: 50px;
}
section:not(:first-child)::after {
  content: '';
  background: #22ECF0;
  position: absolute;
  left: 0;
  top: 0;
  height: 1px;
  width: 80px;
}

@media screen and (max-height: 600px) {
  section {
    min-height: 600px;
  }
}

section:nth-child(2) {
  background: linear-gradient(0deg, rgba(60, 60, 60, 0) 0%, #012539 100%);
}

section.s2 {
  background-color: red;
  opacity: var(--progress);
  transition: all 0.2s ease-in-out;
  transform: translate3d(var(--x), 0, 0);
}

