

.heading {
  padding-bottom: 20px;
}

.heading h1 {
  color: #fff;
  font-family: 'Patua One', cursive;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 48px;
  transition: color 0.2s ease-in-out;
}

.heading span {
  color: #fff;
  transition: color 0.2s ease-in-out;
}

body.flip .heading h1 {
  color: #000;
}

body.flip .heading span {
  color: #555;
}