.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.slot {
  position: relative;
  font-family: 'Patua One', italic;
  font-size: 48px;
  border: 1px solid rgba(34, 236, 240, 0.5);
  border-width: 1px 0 1px 0;
  box-sizing: content-box;
  /* padding: 12px 0; */
  margin: 12px 0;
  clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
  min-width: 300px;
  display: flex;
  justify-content: center;
  height: 90px;
  align-items: center;
  box-sizing: border-box;
}


.strOld, .strNew {
  position: absolute;
  display: inline-block;
}

.strOld span, .strNew span {
  transition: all 0.8s ease-out;
}

.strOld {
  transform: translate(0px, 0px);
}


.strNew span {
  transform: translate(0px, 100px);
}

.strNew span, .strOld span {
  display: inline-block;
  transition: all 0.25s ease-in-out;
}

.anim .strOld span {
  transform: translate(0px, -100px);  
}

.anim .strNew span {
  transform: translate(0px, 0px);  
}


.aside, .headline {
  height: 20px;
  width: 100%;
  display: inline-block;
  text-align: center;
}

.headlineOld, .headlineNew {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  display: block;
  transition: all 0.25s ease-in-out;
}


.headlineNew {
  opacity: 0;
}

.anim .headlineOld {
  opacity: 0;
}

.anim .headlineNew {
  opacity: 1;
}

/** ANIMATIONS **/

.slot {
  opacity: 0;
  height: 0;
  animation: slot 0.75s 1s 1 ease-in-out forwards;
  transform-origin: 50% 50%;
}

.headline, .aside {
  opacity: 0;
  animation: aside 0.5s 1.5s 1 ease-in-out forwards;
}


@keyframes slot {
  0% {
    height: 0;
    opacity: 1;
    top: 45px;
  }

  100% {
    top: 0;
    opacity: 1;
    height: 90px;
  }
}

@keyframes aside {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}