
.wrapper {
  position: fixed;
  z-index: 2000;
  pointer-events: none;
  height: 100%;
  width: 100%;
}

svg.progress {
  position: absolute;
  left: 100px; /* TODO responsive */
  top: 100px;
  opacity: 0.2;
  transition: top 0.8s cubic-bezier(.42,-0.66,.54,1.31), opacity 0.5s ease-in-out;
  /* transition: top 0.8s ease-in-out, opacity 0.5s ease-in-out; */
  will-change: top, opacity;
}

svg.progress .progressCircle {
  transform: rotate(-90deg);
  transform-origin: center;
}

svg.progress text {
  text-anchor: middle;
  fill: #FFF;
  font-size: 18px;
}

svg.scrollDown {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10px;
}

svg.scrollDown .arrows {
  /* animation: name duration timing-function delay iteration-count direction fill-mode; */
  animation: moveDown 1s linear infinite normal forwards;
}

svg.scrollDown .wheel {
  animation: scrollDown 1s linear infinite normal forwards;
}


@keyframes moveDown {
  0% {
    /* transform: translate3d(0, 0px, 0); */
    opacity: 1;
  }

  100% {
    /* transform: translate3d(0, 10px, 0); */
    opacity: 0.2;
  }
}

@keyframes scrollDown {
  0% {
    transform: translate3d(0, 0px, 0);
    opacity: 1;
  }

  100% {
    transform: translate3d(0, 5px, 0);
    opacity: 0.8;
  }
}