.wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
  background: linear-gradient(180deg, rgba(60, 60, 60, 0) 0%, #012539 100%);
  opacity: 0.02;
}

.wrapper::before {
  content: "";
  background: #000;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -2;
}

.wrapper::after {
  content: "";
  /* background-image: url('/assets/img/11326225726_7288e15f6b_o.jpg'); */
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;   
}

.wrapper video {
  opacity: 0.5;
  z-index: 1000;
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  transform: translateX(-50%) translateY(-50%);
  background-size: cover;
  z-index: -1;   
  /* transition: clip-path 0.1s ease-out; */
}

.title {
  position: absolute;
  top: 25%;
  left: 5%;
  transform: translate(0%, -50%);
  /* transition: all 0.2s ease-out; */
