.cursor {
  position: absolute;
  display: block;
  width: 70px;
  height: 70px;
  /* background: rgba(255, 255, 255, 0.2); */
  border: 1px solid rgba(34, 236, 240, 1);
  border-radius: 50%;
  transform-origin: 50% 50%;
  transition: border 0.5s ease-out;
  z-index: 1000;
  pointer-events: none;
  box-sizing: border-box;
}

.cursor.active {
  /* background: white; */
  /* border: 1px solid rgba(255, 255, 255, 0.6); */
  /* background: rgba(255, 255, 255, 0.5); */
  cursor: pointer;
}

.container {
  position: absolute;
  pointer-events: none;
}

.wrapper {
  position: fixed;
  z-index: 1000;
  pointer-events: none;
  height: 100%;
  width: 100%;
}