.databox {
  width: 350px;
  display: grid;
  grid-template-areas: 
    "value unit"
    "title title";
  grid-gap: 1px 20px;
  font-family: 'IBM Plex Sans', sans-serif;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.value {
  grid-area: value;
  font-size: 64px;
  color: #22ECF0;
  padding-bottom: 8px;
  font-weight: 300;
}

.valueSpan {
  grid-area: value / 1 / 1 / 3;
}

.unit {
  grid-area: unit;
  color: #22ECF0;
  padding-bottom: 8px;
  align-self: end;
  font-weight: 300;
  font-size: 18px;
}

.title {
  grid-area: title;
  color: #fff;
  border-top: 1px solid #22ECF0;
  padding-top: 8px;
  font-size: 16px;
  font-weight: 300;
}