.page {
  position: relative;
  height: 80vh;
  width: 80%;
  max-width: 1200px;
  /* outline: 1px dashed cyan; */
  display: flex;
  flex-direction: column;
}

.page .content {
  flex-grow: 1
}

.page.page-Intro .content {
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-gap: 80px;
}

.page.page-Intro .description {
  font-size: 20px;
  font-weight: 700;
  line-height: 40px;
  align-self: start;
}


.page.page-Intro .data-boxes {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

