
@import url('https://fonts.googleapis.com/css?family=Patua+One|Roboto|IBM+Plex+Sans:300');

html, body {
  min-height: 100%;
  /* overflow: hidden; */
  
}

body {
  margin: 0;
  padding: 0;
  /* background-image: radial-gradient(farthest-side at 0 0,rgba(255,26,198,.3),rgba(255,26,198,0)),radial-gradient(farthest-side at 100% 0,rgba(60,221,221,.4),rgba(60,221,221,0)),radial-gradient(farthest-side at 50% 0,rgba(255,26,198,.1),rgba(255,26,198,0)); */
  cursor: grab;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #101010;
  overflow-x: hidden;
  overflow-y: auto;
  color: #fff;
  font-family: 'Roboto', Arial, Helvetica, sans-serif !important;
  transition: background 0.8s ease-in-out;

}

body::-webkit-scrollbar {
  width: 8px;
}
body::-webkit-scrollbar-track {
-webkit-box-shadow: inset 0 0 10px rgba(0,0,0,0.3);
}
body::-webkit-scrollbar-thumb {
background-color: #22ECF0;
/* outline: 1px solid slategrey; */
}

body.flip {
  background: #fff;
}

/* -------------------------------------------------  */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}